      jadeWeight: '',
      tagPriceCode: '',
      jadeTagPrice: '',
      jadeCostPrice: '',
      costPriceCode: '',
      jadeMarketPrice: '',
      jadeQuantity: '',
      orderDate: "",
      validDate: "2023-07-27",
      desStorageId: "",
      desWarehouseId: "",
