import request from '@/utils/request'

export function inbounding(inboundForm) {//
    return request({
        url: '/system/inboundOrder',
        headers: {
            isToken: true
        },
        method: 'post',
        data: inboundForm
    });
}
export function getInboundTypeList() {//
    return request({
        url: '/system/inboundType/list',
        headers: {
            isToken: true
        },
        method: 'get',
    });
}
export function getInboundOrderList(pageNum,pageSize,orderCode,supplierId,desWarehouseId) {//
    return request({
        url: '/system/inboundOrder/list',
        headers: {
            isToken: true
        },
        params: {
            pageNum,pageSize,orderCode,supplierId,desWarehouseId
        },
        method: 'get',
    });
}

export function getOrderCode() {//
    return request({
        url: '/system/inboundOrder/getInboundOrderCode',
        headers: {
            isToken: true
        },
        method: 'get',
    });
}



