import request from '@/utils/request'



export function getWarehouseList() {//
    return request({
        url: '/system/warehouse/list',
        headers: {
            isToken: true
        },
        method: 'get',
    })
}
export function addWarehouse(warehouseForm) {//
    return request({
        url: '/system/warehouse',
        headers: {
            isToken: true
        },
        method: 'post',
        data: warehouseForm
    })
}
export function updateWarehouse(warehouseForm) {//
    return request({
        url: '/system/warehouse',
        headers: {
            isToken: true
        },
        method: 'put',
        data: warehouseForm
        // params:{
        //     userId
        // }
    })
}
export function deleteWarehouse(warehouseId) {//
    return request({
        url: '/system/warehouse/' + warehouseId,
        headers: {
            isToken: true
        },
        method: 'delete',
        // params:{
        //     userId
        // }
    })
}

